import { createClient } from "@/lib/supabase/server"
import { redirect } from "next/navigation"
import { StudentSidebar } from "@/components/student-sidebar"
import SubjectsView from "@/components/subjects-view";

export default async function StudentSubjectsPage() {
  const supabase = await createClient()
  const {
    data: { user },
  } = await supabase.auth.getUser()

  if (!user) {
    redirect("/auth/login")
  }

  const { data: profile } = await supabase.from("profiles").select("role").eq("id", user.id).single()

  if (!profile || profile.role !== "student") {
    redirect("/student")
  }

  return (
    <div className="flex h-screen bg-background">
      <StudentSidebar />
      <div className="flex-1 overflow-auto">
        <SubjectsView />
      </div>
    </div>
  )
}

// --- SubjectsView Component ---
"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Progress } from "@/components/ui/progress"
import { Loader2, BookOpen, ChevronRight } from "lucide-react"
import Link from "next/link"

interface Subject {
  id: string
  name: string
  description: string
  icon?: string
}

interface Topic {
  id: string
  subject_id: string
  name: string
  description: string
}

interface TopicStats {
  topicId: string
  topicName: string
  progress: number
  status: "MASTERED" | "GOOD" | "NEEDS_WORK" | "Not Started"
}

interface SubjectWithTopics {
  subject: Subject
  topics: TopicStats[]
  completedTopics: number
  totalTopics: number
  averageProgress: number
}

export default function SubjectsView() {
  const [studentId, setStudentId] = useState<string | null>(null)
  const [subjects, setSubjects] = useState<SubjectWithTopics[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const supabase = createClient()

  useEffect(() => {
    const fetchSubjects = async () => {
      try {
        setLoading(true)

        // Get current user
        const {
          data: { user },
        } = await supabase.auth.getUser()

        if (!user?.id) {
          throw new Error("User not authenticated")
        }

        setStudentId(user.id)

        // Fetch all subjects
        const { data: subjectsData, error: subjectsError } = await supabase
          .from("subjects")
          .select("*")
          .order("name", { ascending: true })

        if (subjectsError) throw new Error(`Failed to fetch subjects: ${subjectsError.message}`)

        if (!subjectsData || subjectsData.length === 0) {
          setSubjects([])
          setLoading(false)
          return
        }

        // For each subject, fetch topics and their progress
        const subjectsWithTopics: SubjectWithTopics[] = await Promise.all(
          subjectsData.map(async (subject) => {
            try {
              // Fetch topics for this subject
              const { data: topicsData, error: topicsError } = await supabase
                .from("topics")
                .select("id, name, description")
                .eq("subject_id", subject.id)
                .order("name", { ascending: true })

              if (topicsError) {
                console.warn(`Error fetching topics for subject ${subject.id}:`, topicsError)
                return {
                  subject,
                  topics: [],
                  completedTopics: 0,
                  totalTopics: 0,
                  averageProgress: 0,
                }
              }

              if (!topicsData || topicsData.length === 0) {
                return {
                  subject,
                  topics: [],
                  completedTopics: 0,
                  totalTopics: 0,
                  averageProgress: 0,
                }
              }

              // Fetch progress for all topics in this subject
              const topicIds = topicsData.map((t) => t.id)
              const { data: progressData, error: progressError } = await supabase
                .from("topic_progress")
                .select("topic_id, progress_percentage, status")
                .eq("student_id", user.id)
                .in("topic_id", topicIds)

              if (progressError) {
                console.warn(`Error fetching progress for subject ${subject.id}:`, progressError)
              }

              // Map topics with their progress
              const topicsWithStats: TopicStats[] = topicsData.map((topic) => {
                const progress = progressData?.find((p) => p.topic_id === topic.id)
                return {
                  topicId: topic.id,
                  topicName: topic.name,
                  progress: progress?.progress_percentage || 0,
                  status: (progress?.status as any) || "Not Started",
                }
              })

              const completedTopics = topicsWithStats.filter((t) => t.status === "MASTERED").length
              const averageProgress =
                topicsWithStats.length > 0
                  ? Math.round(topicsWithStats.reduce((sum, t) => sum + t.progress, 0) / topicsWithStats.length)
                  : 0

              return {
                subject,
                topics: topicsWithStats,
                completedTopics,
                totalTopics: topicsWithStats.length,
                averageProgress,
              }
            } catch (err) {
              console.error(`Error processing subject ${subject.id}:`, err)
              return {
                subject,
                topics: [],
                completedTopics: 0,
                totalTopics: 0,
                averageProgress: 0,
              }
            }
          })
        )

        setSubjects(subjectsWithTopics)
      } catch (err) {
        const errorMsg = err instanceof Error ? err.message : "Failed to load subjects"
        setError(errorMsg)
        console.error("Error fetching subjects:", err)
      } finally {
        setLoading(false)
      }
    }

    fetchSubjects()
  }, [supabase])

  if (loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <Loader2 className="w-8 h-8 animate-spin" />
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto p-6">
        <Card className="border-destructive">
          <CardContent className="p-6">
            <p className="text-destructive">Error: {error}</p>
          </CardContent>
        </Card>
      </div>
    )
  }

  if (subjects.length === 0) {
    return (
      <div className="container mx-auto p-6">
        <div className="text-center">
          <BookOpen className="w-12 h-12 mx-auto text-muted-foreground mb-4" />
          <h1 className="text-2xl font-bold mb-2">No Subjects Available</h1>
          <p className="text-muted-foreground">Subjects will appear here once your instructor creates them.</p>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto p-6">
      <div className="mb-8">
        <h1 className="text-3xl font-bold">ðŸ“š Subjects</h1>
        <p className="text-muted-foreground mt-2">Explore and master your subjects</p>
      </div>

      <div className="grid gap-6">
        {subjects.map((subjectData) => (
          <Card key={subjectData.subject.id} className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <div className="flex justify-between items-start">
                <div className="flex-1">
                  <CardTitle className="text-2xl">{subjectData.subject.name}</CardTitle>
                  <CardDescription className="mt-2">{subjectData.subject.description}</CardDescription>
                </div>
                <Badge variant="outline">
                  {subjectData.completedTopics}/{subjectData.totalTopics} topics
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Subject Progress */}
              <div className="space-y-2">
                <div className="flex justify-between items-center">
                  <span className="text-sm font-medium">Overall Progress</span>
                  <span className="text-sm text-muted-foreground">{subjectData.averageProgress}%</span>
                </div>
                <Progress value={subjectData.averageProgress} className="h-2" />
              </div>

              {/* Topics List */}
              {subjectData.totalTopics > 0 ? (
                <div className="space-y-2">
                  <h4 className="text-sm font-semibold mb-3">Topics</h4>
                  {subjectData.topics.map((topic) => (
                    <Link
                      key={topic.topicId}
                      href={`/student/subjects/${topic.topicId}`}
                    >
                      <div className="flex items-center justify-between p-3 rounded-lg border hover:bg-muted/50 transition-colors cursor-pointer group">
                        <div className="flex-1">
                          <p className="font-medium text-sm group-hover:text-primary transition-colors">
                            {topic.topicName}
                          </p>
                          <div className="flex items-center gap-2 mt-1">
                            <Progress value={topic.progress} className="h-1 flex-1 max-w-xs" />
                            <span className="text-xs text-muted-foreground">{topic.progress}%</span>
                          </div>
                        </div>
                        <div className="flex items-center gap-2 ml-4">
                          {topic.status === "MASTERED" && (
                            <Badge className="bg-green-100 text-green-800">âœ“ Mastered</Badge>
                          )}
                          {topic.status === "GOOD" && (
                            <Badge className="bg-blue-100 text-blue-800">Good</Badge>
                          )}
                          {topic.status === "NEEDS_WORK" && (
                            <Badge className="bg-yellow-100 text-yellow-800">Needs Work</Badge>
                          )}
                          {topic.status === "Not Started" && (
                            <Badge variant="outline">Not Started</Badge>
                          )}
                          <ChevronRight className="w-4 h-4 text-muted-foreground group-hover:text-primary transition-colors" />
                        </div>
                      </div>
                    </Link>
                  ))}
                </div>
              ) : (
                <p className="text-sm text-muted-foreground text-center py-4">
                  No topics available for this subject yet.
                </p>
              )}
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
